.feedback {
  position: relative;
  padding-top: 90px;
  padding-bottom: 115px;
  display: flex;
  // color: $alternate-font-color;
  // background-color: $form-bg-color;

  background-repeat: no-repeat;
  background-position: calc(70% + 510px) -20px;
  background-image: url("../img/triangles.svg");

  &::before,
  &::after {
    content: "";
    position: absolute;
    overflow: hidden;
    width: 665px;
    height: 487px;
    left: calc((50% - 860px) * 2);
    background-repeat: no-repeat, no-repeat;
    background-size: auto, contain;
    background-image: url("../img/airplane.png"), url("../img/triangle-double.svg");
    background-position: 155px 65px, 0 0;
  }

  &__wrapper {
    width: 94%;
    max-width: 570px;
    margin-right: 19.5%;
    margin-left: auto;
  }

  h2 {
    @include indents-reset;
    margin-bottom: 7px;
    // font-family: $alternate-font;
    // font-weight: 400;
    font-size: 36px;
    line-height: 51px;
  }

  p {
    @include indents-reset;
    margin-bottom: 50px;
  }

  &__fields-list {
    display: flex;
    justify-content: space-between;
  }

  &__fields-item {
    width: 270px;
    box-sizing: border-box;

    // &--checkbox {
    //   position: relative;
    //   padding-left: 50px;
    // }
  }

  &__button {
    width: 100%;
  }

  form {
    fieldset {
      margin-bottom: 40px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    // input[type="checkbox"] + label {
    //   width: 100%;
    //   font-size: 14px;
    //   line-height: 16px;
    //   color: $form-label-font-color;

    //   &::before,
    //   &::after {
    //     content: "";
    //     position: absolute;
    //     box-sizing: border-box;
    //   }

    //   &::before {
    //     width: 16px;
    //     height: 16px;
    //     top: 7px;
    //     left: 15px;
    //     border: 1px solid $main-bg-color;
    //   }

    //   &::after {
    //     display: none;
    //     width: 8px;
    //     height: 5px;
    //     top: 13px;
    //     left: 19px;
    //     background-position: center center;
    //     background-repeat: no-repeat;
    //     background-image: url("../img/bg-icon-check.svg");
    //   }
    // }

    // input[type="checkbox"] {
    //   &:checked + label {
    //     &::after {
    //       display: block;
    //     }
    //   }
    // }
  }
}

@media (max-width: $tablet-width) {
  .feedback {
    padding-left: 90px;
    padding-right: 90px;
    padding-top: 80px;
    padding-bottom: 100px;

    &__wrapper {
      width: 100%;
      max-width: 592px;
      margin: 0 auto;
    }

    h2 {
      font-size: 32px;
      line-height: 140.6%;
      margin-bottom: 18px;
    }

    p {
      margin-bottom: 57px;
    }
  }
}

@media (max-width: $mobile-width) {
  .feedback {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 33px;
    padding-bottom: 40px;

    h2 {
      font-size: 28px;
      margin-bottom: 10px;
    }

    p {
      margin-bottom: 28px;
    }

    &__fields-list {
      flex-direction: column;
    }

    &__fields-item {
      width: 100%;

      margin-bottom: 32px;

      &--checkbox {
        padding-left: 35px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    form {
      fieldset {
        margin-bottom: 32px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      textarea {
        height: 105px;
      }

      input[type="checkbox"] + label {
        &::before {
          top: 0;
          left: 0;
        }

        &::after {
          top: 4px;
          left: 5px;
        }
      }
    }
  }
}
